import React from 'react';
import { useNavigate } from 'react-router-dom';
import { useLanguage } from '../../contexts/LanguageContext';
import './CounsellingPsychotherapy.css';

const CounsellingPsychotherapy = () => {
  const navigate = useNavigate();
  const { t } = useLanguage();
  return (
    <div className="counselling-psychotherapy">
      {/* Hero Section */}
      <section className="hero-section">
        <div className="hero-content">
          <div className="hero-text">
            <h1>{t('counsellingPsychotherapyTitle')}</h1>
            <p>
              {t('counsellingHeroDesc1')}
            </p>
            <p>
              {t('counsellingHeroDesc2')}
            </p>
            <button 
              className="appointment-btn"
              onClick={() => navigate("/contact")}
            >
              {t('bookAppointment')}
            </button>
          </div>
          <div className="hero-image">
            <img src="/images/counselling-psychotherapy-nature.jpg" alt="Nature counselling" />
          </div>
        </div>
      </section>

      {/* Content Section */}
      <section className="content-section">
        <div className="content-wrapper">
          <div className="content-image">
            <img src="/images/counselling-psychotherapy.jpg" alt={t('counsellingPsychotherapyTitle')} />
          </div>
          <div className="content-text">
            <p>
              {t('counsellingContentDesc1')}
            </p>
            <p>
              {t('counsellingContentDesc2')}
            </p>
            <p>
              {t('counsellingContentDesc3')}
            </p>
            <button 
              className="appointment-btn"
              onClick={() => navigate("/contact")}
            >
              {t('bookAppointment')}
            </button>
          </div>
        </div>
      </section>
    </div>
  );
};

export default CounsellingPsychotherapy;
